    }
  }
}

.footer-box {
  width: 100%;
  height: 340px;
  background: #0042c1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  .footer-top {
    display: flex;
    justify-content: space-between;
    width: 1280px;
    margin: auto;

    div {
      width: 330px;
      position: relative;

      img {
        width: 40px;
        height: 40px;
        // background: #FFFFFF;
        margin-right: 40px;
      }

      p {
        font-size: 14px;
        font-family: PingFangSC, PingFang SC;
        font-weight: 400;
        color: #ffffff;
        line-height: 20px;
      }

      p:nth-child(1) {
        font-size: 16px;
        font-family: PingFangSC, PingFang SC;
        font-weight: 500;
        color: #ffffff;
        line-height: 22px;
        margin-bottom: 25px;
      }

      .code {
        width: 144px;
        height: 166px;
        background: #ffffff;
        box-shadow: 0px 2px 8px 0px rgba(0, 0, 0, 0.15);
        padding: 17px;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        border-radius: 8px;
        position: absolute;
        top: -176px;
        right: 20px;

        span {
          font-size: 14px;
          font-family: PingFangSC, PingFang SC;
          font-weight: 400;
          color: rgba(0, 0, 0, 0.65);
        }

        > img {
          width: 110px;
          height: 110px;
          background: #d8d8d8;
          border-radius: 8px;
          margin-right: 0;
          margin-bottom: 10px;
        }
      }

      .code ::after {
        content: "";
        display: inline-block;
        width: 0;
        height: 0;
        border: 4px solid rgba(0, 0, 0, 0);
        position: absolute;
        bottom: -8px;
        left: 8px;
        border-top-color: #fff;
      }
    }
  }

  .footer-bot {
    width: 100%;
    text-align: center;

    p {
      height: 60px;
      line-height: 60px;
      color: #fff;
      font-size: 14px;
      font-family: PingFangSC, PingFang SC;
      font-weight: 400;
      color: #ffffff;

      span {
        margin-left: 20px;
      }
    }

    p:nth-child(1) {
      border-bottom: 1px solid #fff;
    }
  }
}

@media screen and (max-width: 1080px) {
