          left: 8px;
          border-top-color: #fff;
        }
      }

      > div:nth-child(3) {
        margin-bottom: 0;
      }
    }

    .footer-bot {
      width: 108rem;
      text-align: center;

      p {
        height: 15.4rem;
        line-height: 15.4rem;
